
* {
    margin: 0; 
    padding: 0;
}
body {
    font: 14px/1.5 Tahoma,Helvetica,Arial, "Microsoft YaHei", "Hiragino GB", Sans-serif;
    text-align: center;
    min-width: 450px;
}
ul {
    width: 450px;
    margin: 10px auto;
    text-align: left;
    line-height: 2;
    list-style-type: none;
}
input[type='checkbox'],
button {
    cursor: pointer;
}
/*待办内容*/
.todoItem input {
    vertical-align: text-bottom;
    margin-bottom: 2px;
}
.todoItemBtn {
    float: right;
}
.todoItemBtn button {
    padding: 0 5px;
    margin-right: 10px;
    border: .5px solid #ccc;
    outline-width: 0;
    background: #fff;
}
.todoItemBtn button:hover {
    background: #c11b17;
    color: #fff;
}
/*已完成*/
.hasDone {
    text-decoration: line-through;
    background: #fcc;
}
.finishedTime {
    padding-left: 15px;
    font: 12px/1 "Hiragino GB";
    color: #2b65ec;
    font-style: italic;
}
/*统计*/
.summarize {
    font: 16px/2 "Hiragino GB";
}
.summarize strong {
    color: #f00;
}
/*过渡动画*/
.todoUl-enter,
.todoUl-leave-to {
    -webkit-transform: translateX(-999px);
            transform: translateX(-999px);
    opacity: 0;
}
.todoUl-leave-active {;
    -webkit-transition: all .5s ease;;
    transition: all .5s ease;
    position: absolute;
}
.todoUl-enter-active {
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}
.todoUl-move {
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}

